import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/img/header-img.svg";
import { ArrowRightCircle } from "react-bootstrap-icons";
import "animate.css";
import TrackVisibility from "react-on-screen";

export const Banner = () => {
  const [text, setText] = useState("I am a frontend developer");
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const timer = setInterval(() => {
      setIndex((prevIndex) => prevIndex + 1);
    }, 100);
    return () => clearInterval(timer);
  }, []);

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <span className="tagline">Welcome to my Portfolio</span>
            <h1>{text.substring(0, index)}</h1>
            <p>
              I am a skilled frontend developer from India,having expertise in
              JavaScript - React, and Next js. I'm also familiar with NodeJS,
              TypeScript, Git, Rest API, Redux, Styled Components, Tailwindcss,
              Bootstrap, and many other libraries. I can create responsive,
              fast, and user-friendly web apps. I am passionate about web
              technologies and web apps. I have experience of working with
              several startups, and currently I am working as a freelancer web
              developer in a company.
            </p>
            <a
              href="https://drive.google.com/file/d/1r52AN9Vvt1R2x744IZFvpQIx3C14qwor/view?usp=sharing"
              target="_blank"
              className="text-decoration-none"
              rel="noopener noreferrer"
            >
              <button>
                View my Resume
                <ArrowRightCircle size={25} />
              </button>
            </a>
          </Col>
          <Col xs={12} md={6} xl={5}>
            <div>
              <img src={headerImg} alt="Header Img" />
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};
